.colors {
  display: flex;
  justify-content: flex-start;
  align-items: center; }
  .colors div {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    margin-right: 20px; }
    .colors div:last-child {
      margin-right: 0; }

.white {
  border: 1px solid #b1b1b1;
  background: #fff; }

.red {
  background: #c33e16; }

.black {
  background: #201d17; }

/* I E   S T Y L E S */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .block {
    width: 100%; } }

.ov-hidden {
  overflow: hidden; }

@media screen and (min-width: 992px) {
  .card-img-small-item:hover {
    box-shadow: 0 0 0 5px #f0b328; } }

/* 
    M O B I L E
*/
@media screen and (max-width: 1200px) {
  html,
  body {
    min-width: 320px;
    width: 100%;
    overflow-x: hidden; }
  a:focus {
    outline: none; } }
